import React, {useEffect} from 'react';
import AboutUsContentComponent from "./AboutUsContentComponent";
import {connect} from "react-redux";
import {bindActionCreators} from "redux";
import {entityActions} from "../../actions";

function AboutUsContentContainer({language, getEntity, entity, entityReference, loading}){
    const $entityReference = 'aboutus_page';

    useEffect(() => getEntity($entityReference), [getEntity]);

    if(entityReference !== $entityReference) {
        loading = true
    }

    return <AboutUsContentComponent {...{...entity, loading, language}} />
}

const mapStateToProps = ({language, entity}) => ({
    entity: entity.entity && entity.entity.categoryFields,
    entityReference: entity.entity && entity.entity.categoryreference,
    loading: entity.loading,
    language: language.currentLanguage
});

const mapDispatchToProps = dispatch => ({
    ...bindActionCreators(entityActions, dispatch)
});

const connectedAboutUsContent = connect(
  mapStateToProps,
  mapDispatchToProps)(AboutUsContentContainer);
export { connectedAboutUsContent as AboutUsContent };

