import React from 'react';
import './header.scss';
import {NavLink} from "react-router-dom";
import MobileHeaderComponent from "./MobileHeaderComponent";
import Logo from './logo.svg';
import Container from "@material-ui/core/Container";

function HeaderComponent({ setLanguage, language, basicInfo }){
  const nav = basicInfo && basicInfo.header.nav;
    return(
        <header id="header">
            <Container maxWidth="lg" className="header-container container">
                <div className="logo">
                    <NavLink exact to="/">
                      <img src={Logo} alt="Logo" />
                    </NavLink>
                </div>
                <MobileHeaderComponent />
                <div className='wrapper-nav-select'>
                    <nav className="header-navbar">
                        {
                            basicInfo && nav.map((e, index) => {
                                return  <NavLink key={index} exact={e.exact} to={e.path}>{e.name[language]}</NavLink>
                            })
                        }
                    </nav>
                    <select defaultValue={language} name="language" id="languages" onChange={(e) => setLanguage(e.target.value)}>
                        <option value="1">En</option>
                        <option value="2">Ru</option>
                        <option value="3">Am</option>
                    </select>
                </div>

            </Container>
        </header>
    )
}

export default HeaderComponent;
