import React, {useEffect} from 'react';
import ContactsContentComponent from "./ContactsContentComponent";
import {bindActionCreators} from "redux";
import {entityActions} from "../../actions";
import {connect} from "react-redux";

function ContactsContentContainer({language, getEntity, entity, entityReference, loading}){
    const $entityReference = 'contacts_page';

    //useEffect(() => getEntity($entityReference), [getEntity]);

    if(entityReference !== $entityReference) {
        loading = true
    }

    return <ContactsContentComponent {...{entity, language, loading}} />
}

const mapStateToProps = ({language, entity}) => ({
    entity: entity.entity && entity.entity.categoryFields,
    entityReference: entity.entity && entity.entity.categoryreference,
    loading: entity.loading,
    language: language.currentLanguage
});

const mapDispatchToProps = dispatch => ({
    ...bindActionCreators(entityActions, dispatch)
});

const connectedContactsContentContainer = connect(
    mapStateToProps,
    mapDispatchToProps)(ContactsContentContainer);

export { connectedContactsContentContainer as ContactsContent };

