import React, {useEffect} from 'react';
import HomePageComponent from "./HomePageComponent";
import {bindActionCreators} from "redux";
import {entityActions} from "../../actions";
import {connect} from "react-redux";

function HomePageContainer({ getEntity, entity, entityReference, loading, language }){
    const $entityReference = 'home_page';

    useEffect(() => getEntity($entityReference), [getEntity]);

    if(entityReference !== $entityReference) {
        loading = true
    }

    return <HomePageComponent {...{...entity, loading, language}} />
}


const mapStateToProps = ({ entity, language }) => ({
    entity: entity.entity && entity.entity.categoryFields,
    entityReference: entity.entity && entity.entity.categoryreference,
    loading: entity.loading,
    language: language.currentLanguage
});

const mapDispatchToProps = dispatch => ({
    ...bindActionCreators(entityActions, dispatch),
});

const connectedHomePageContainer = connect(
    mapStateToProps,
    mapDispatchToProps
)(HomePageContainer);


export {connectedHomePageContainer as Home};
